html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.7rem;
}

h3 {
  font-size: 3rem;
}

h1, h2, h3, h4 {
  font-family: "Bebas Neue", cursive;
  font-weight: 300;
}

.container {
  margin: 0 auto;
  width: 95%;
  max-width: 130rem;
}

.section {
  padding: 5rem 0;
}

.body-page {
  background-color: #F5F5F5;
}

.body.hidden {
  overflow: hidden;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 99;
}

.fa-whatsapp {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-whatsapp:hover {
  color: #25d366;
}

.fa-facebook-square {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-facebook-square:hover {
  color: #2C88FF;
}

.fa-map-marker-alt {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-map-marker-alt:hover {
  color: #DE432A;
}

.fa-instagram {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-instagram:hover {
  color: #C13584;
}

.fa-envelope {
  color: #fff;
  transition: 0.2s ease-in-out;
}
.fa-envelope:hover {
  color: #FFB805;
}

.fa-phone-alt {
  font-size: 1.3rem;
}

.info-bar {
  margin-bottom: 3rem;
  background-color: #000;
  color: #fff;
  font-size: 3.5vw;
  z-index: 999;
}
@media (min-width: 480px) {
  .info-bar {
    font-size: 1.6rem;
  }
}
@media (min-width: 1024px) {
  .info-bar {
    margin-bottom: 2rem;
  }
}
.info-bar p {
  margin: 0;
}
.info-bar .info {
  display: flex;
  gap: 1rem;
}

.content-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.content-bar .help a {
  color: #fff;
}
.content-bar .location {
  display: none;
}
@media (min-width: 1024px) {
  .content-bar .location {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
}

.social-media .logos {
  display: none;
}
@media (min-width: 1024px) {
  .social-media .logos {
    margin-right: 0.5rem;
    display: flex;
    gap: 1rem;
  }
}
.social-media .phone-info {
  margin-left: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.header {
  background-color: #2e005a;
  padding-bottom: 3rem;
  z-index: 90;
  top: 0;
}
@media (min-width: 1024px) {
  .header {
    padding-bottom: 2rem;
  }
}

.barra-header {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .barra-header {
    display: flex;
    justify-content: space-between;
  }
}

.logo {
  justify-self: center;
  width: 23rem;
  grid-column: 1/7;
}
@media (min-width: 768px) {
  .logo {
    flex: 0 0 22rem;
  }
}

.hamburger-menu {
  grid-column: 1/2;
  justify-self: self-start;
}

.search-bar-container {
  border-radius: 4rem;
  background-color: #fff;
  height: 4rem;
  padding: 1rem 1.5rem;
  transition: 0.2s ease-in-out;
  grid-column: 2/5;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .search-bar-container {
    grid-column: 2/6;
  }
}
@media (min-width: 1024px) {
  .search-bar-container {
    margin-right: 1.5rem;
  }
}
.search-bar-container .search-bar {
  padding: 0;
  border: none;
  background: none;
  outline: none;
  width: 100%;
  transition: 0.5s ease-in-out;
  color: #afafaf;
}
.search-bar-container .button-search {
  color: #2e005a;
  background-color: #fff;
  border-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.user-cart {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-self: end;
  grid-column: 5/7;
}
@media (min-width: 768px) {
  .user-cart {
    grid-column: 6/7;
    gap: 2rem;
  }
}
.user-cart a {
  color: #fff;
  font-size: 3rem;
}

.user #user-icon {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  overflow: hidden;
  outline: none;
  position: relative;
  transition: 0.2s ease-in-out;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}
.user #user-icon:hover {
  color: #F512F1;
}

.shopping-cart {
  position: relative;
  transition: 0.2s ease-in-out;
}
.shopping-cart:hover .items-cart {
  color: #fff;
}
.shopping-cart:hover .fa-shopping-cart {
  color: #F512F1;
}
.shopping-cart .fa-shopping-cart {
  transition: ease-in-out 0.2s;
  color: #fff;
  font-size: 3rem;
}
.shopping-cart .items-cart {
  color: #F512F1;
  text-align: center;
  font-size: 1.3rem;
  position: absolute;
  font-weight: 700;
  border-radius: 50%;
  line-height: 1.5;
  top: 0.2rem;
  right: 0;
  left: 0.5rem;
  transition: 0.2s ease-in-out;
}

.hamburger-menu {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .hamburger-menu {
    display: none;
  }
}
.hamburger-menu .line {
  height: 0.3rem;
  width: 2.5rem;
  background-color: #fff;
  margin: 0.5rem 0;
  border-radius: 1rem;
  transition: all 0.3s ease;
}

.toggle.line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle.line2 {
  opacity: 0;
}

.toggle.line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

.user {
  position: relative;
}

.login-window {
  box-shadow: 0rem 0rem 0.3rem #444;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  padding: 1rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 5rem;
  right: -4rem;
  width: 25rem;
  height: 15rem;
  background-color: #957DAD;
  color: #fff;
  z-index: 1000;
}
@media (min-width: 1024px) {
  .login-window {
    top: 6rem;
  }
}
.login-window .buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.login-window .buttons .button {
  color: #fff;
  font-size: 1.6rem;
  background-color: #2e005a;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  transition: 0.2s linear;
}
.login-window .buttons .button:hover {
  background-color: #fff;
  color: #2e005a;
}
.login-window .close-button {
  font-size: 2.5rem;
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  transition: 0.2s ease-in-out;
}
.login-window .close-button:hover {
  color: #2e005a;
}
.login-window .text-user {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  display: block;
  margin: 0 0 1.5rem 0;
}
.login-window .text-user .name-user {
  display: block;
}

.bar-nav .main-nav {
  font-weight: 700;
  position: fixed;
  width: 70%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #2e005a;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform ease 0.5s;
  transform: translate(-120%, 0);
  z-index: 200;
}
.bar-nav .main-nav .close-nav {
  position: absolute;
  top: 30rem;
  right: -1.5rem;
  font-size: 2.5rem;
  background-color: #fff;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  display: grid;
  place-items: center;
}
@media (min-width: 1024px) {
  .bar-nav .main-nav {
    padding: 3rem 0;
    display: flex;
    transform: translate(0, 0);
    position: static;
    transition: none;
    flex-direction: row;
    width: auto;
    justify-content: center;
    gap: 5rem;
  }
}
.bar-nav .main-nav a {
  margin-bottom: 5rem;
  height: 5rem;
  text-transform: uppercase;
  font-size: 1.6rem;
  color: #fff;
  transition: 0.2s ease-in-out;
}
.bar-nav .main-nav a:hover {
  color: #F512F1;
}
@media (min-width: 1024px) {
  .bar-nav .main-nav a {
    height: auto;
    margin-bottom: 0;
  }
}
.bar-nav .main-nav i {
  font-size: 1.3rem;
}

.bar-nav .main-nav.is-active {
  transform: translate(0, 0);
  -webkit-box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
  -moz-box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
  box-shadow: 10px 0px 23px -12px rgba(0, 0, 0, 0.86);
}

.social-media-bar {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
  margin: 5rem 0 10rem 0;
}
.social-media-bar .social-media-logos i {
  cursor: pointer;
  font-size: 1.8rem;
  margin-right: 1rem;
}
.social-media-bar .social-media-logos i:last-of-type {
  margin: 0;
}
.social-media-bar .phone-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #fff;
}
.social-media-bar p {
  color: #fff;
}

.swiper {
  height: 50vw;
}
@media (min-width: 1024px) {
  .swiper {
    max-width: 150rem;
    height: 45rem;
  }
}
.swiper .swiper-button-next, .swiper .swiper-button-prev {
  visibility: hidden;
}
@media (min-width: 1024px) {
  .swiper .swiper-button-next, .swiper .swiper-button-prev {
    visibility: visible;
  }
}
.swiper .swiper-button-next.black {
  color: #000;
}
.swiper .swiper-button-prev.black {
  color: #000;
}
.swiper .swiper-button-prev.white {
  color: #fff;
  left: 1.4rem;
}
.swiper .swiper-button-next.white {
  color: #fff;
  right: 1.4rem;
}
.swiper .swiper-pagination-bullet {
  background: #2e005a;
  opacity: 0.4;
}
.swiper .swiper-pagination-bullet-active {
  background: #2e005a;
  opacity: 1;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#image1, #image2, #image3, #image4 {
  background-color: gray;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.more-about-us {
  text-align: center;
  background-color: #fff;
}

@media (min-width: 768px) {
  .info-bonus {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.info-bonus .info-box {
  text-align: center;
  margin-bottom: 5rem;
}
.info-bonus .info-box:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .info-bonus .info-box {
    flex: 0 0 calc(33.3% - 5rem);
    margin-bottom: 0;
  }
}
.info-bonus .info-box .img-info {
  width: 10rem;
  margin: 0 auto;
}
.info-bonus .info-box p {
  font-size: 1.5rem;
}
.info-bonus .info-box h3 {
  font-size: 2rem;
  font-weight: 400;
}
.info-bonus .info-box a {
  font-size: 1.4rem;
  opacity: 0.5;
}

.most-popular-pinatas {
  text-align: center;
}
.most-popular-pinatas h3 {
  margin: 0;
}

.grid-popular-pinatas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.card-pinata {
  background-color: #fff;
  padding: 1rem;
}
.card-pinata .btn-add-cart {
  background-color: #fff;
  padding: 1rem;
  color: #2e005a;
  font-size: 1.5rem;
  border-radius: 1rem;
  border-color: #2e005a;
}
.card-pinata .name-pinata {
  font-size: 2rem;
}